import React from "react";
import styled from "styled-components";

// Animations
import { motion } from "framer-motion";
import { pageAnimation, titleAnim } from "../animation";

const ContactUs = () => {
  return (
    <ContactStyle
      variants={pageAnimation}
      initial="hidden"
      animate="show"
      exit="exit"
      style={{ background: "#fff" }}
    >
      <Title>
        <Hide>
          <motion.h2 variants={titleAnim}>Get in touch</motion.h2>
        </Hide>
      </Title>
      <div>
        <Hide>
          <Social variants={titleAnim}>
            <Circle />
            <h2>Send Us A Message</h2>
          </Social>
        </Hide>
        <Hide>
          <Social variants={titleAnim}>
            <Circle />
            <h2>Send an email.</h2>
          </Social>
        </Hide>
        <Hide>
          <Social variants={titleAnim}>
            <Circle />
            <h2>Social media.</h2>
          </Social>
        </Hide>
      </div>
    </ContactStyle>
  );
};

const ContactStyle = styled(motion.div)`
  padding: 5rem 10rem;
  color: #353535;
  min-height: 90vh;

  @media (max-width: 1500px) {
    padding: 2rem;
    font-size: 1rem;
  }

  @media (max-width: 380px) {
    padding: 2rem 0.6rem;
  }
`;

const Title = styled.div`
  margin-bottom: 4rem;
  color: #000;
  @media (max-width: 1500px) {
    margin-top: 5rem;
  }
`;

const Hide = styled.div`
  overflow: hidden;
`;

const Circle = styled.div`
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  background: #353535;

  @media (max-width: 620px) {
    width: 2rem;
    height: 2rem;
  }

  @media (max-width: 380px) {
    width: 1.5rem;
    height: 1.5rem;
  }
`;

const Social = styled(motion.div)`
  display: flex;
  align-items: center;
  h2 {
    margin: 2rem;

    @media (max-width: 620px) {
      font-size: 2rem;
    }

    @media (max-width: 380px) {
      font-size: 1.5rem;
    }
  }
`;

export default ContactUs;
